<template>
  <div class="account-edit-address__container account-page">
    <div class="account-page__header">
      <div class="account-page__header-title account-page__title">
        <h2>{{ $t("editAddress") }}</h2>
      </div>
    </div>
    <div class="account-edit-address__content">
      <MyAddressFormRepresentation :onSubmit="onSubmit" :address="address" />
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import addressMixin from "../../mixins/addressMixin.js";
import MyAddressFormRepresentation from "@/components/MyAddressFormRepresentation";

export default {
  name: "ViewMyAccountEditAddress",
  mixins: [addressMixin],
  data() {
    return {
      address: {}
    };
  },
  components: {
    MyAddressFormRepresentation
  },
  props: {
    addressId: String
  },
  computed: {
    ...mapGetters(["listAddress"])
  },
  mounted() {
    const address = this.listAddress.find(
      address => address.entity_id === this.addressId
    );
    this.address = !address ? this.transform({}) : this.transform(address);
  }
};
</script>

